/* QRCodeGenerator.css */

body {
    margin: 0;
    padding: 0;
    overflow: hidden; /* Prevent scrolling */

  }

.App {
    background-color: rgb(138, 237, 138); /* Set the background color for the entire page */
    height: 97vh; /* Set the container height to fill the viewport */
    display: flex;
    border: 10px solid rgb(2, 156, 43); /* Add a solid black border */
  }
  

.QRCodeGenerator-container {
    text-align: center;
    margin: 100px 200px;
    background-color: rgb(161, 238, 184); /* Set the background color to dark green */
    padding: 100px;
    border-radius: 100px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    margin-left: 350px; /* Adjust this value to shift to the right */

  }
  
  .QRCodeGenerator-heading {
    color: #1059bf; /* Set the heading color to pastel blue */
    margin-bottom: 15px;
    font-family: 'Black Ops One' ;
  }
  
  .QRCodeGenerator-input {
    width: 100%;
    padding: 10px;
    margin-bottom: 10px;
  }
  